body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* NAV */
li{
  list-style: none;
}

.flex-row{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header-nav{
  background-color: blue;
  border-bottom: 1px solid #090a0a;
  padding: 5px;
}

.header-nav li{
  color: white;
  padding-right: 15px
  
}

.header-nav a{
  color: white
}

.header-nav li:hover{
  text-decoration: none;
  color: #e0876a;
}


/* Projects */

.project:hover {
  background-color: #fbefcc;  
} 

.card-text:hover {
  text-decoration: none;
  color: #e0876a;
}

.card-img-top {
  height: 200px;
  padding: 10px;
}

/* footer */
.footer{
  background-color: blue;
  border-top: 2px solid #43FFF4;
  padding: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.footer img {
  padding: 10px;
} 

@media (max-width: 544px) {  
  .about-me {
    font-size: calc(30% + 1vw + 1vh);
  }
}